import { Controller, Node, Text } from '@ali/4ever-cangjie';

/**
 * 判断是否渲染 placeholder
 * @param controller
 * @param node
 */
export function isPlaceholderVisible(controller: Controller, node: Node) {
  const { value } = controller;
  const { document } = value;
  const first = document.nodes[0];
  return (
    document.nodes.length === 1 &&
    node.key === first.key &&
    first.type === 'paragraph' &&
    !first.data.blockquote &&
    (first.data.jc || 'left') === 'left' &&
    !first.data.ind &&
    !first.data.list &&
    Text.isTextList(first.nodes) &&
    first.text === ''
  );
}
